@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Poppins:700");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  color: #111111;
  font-size: 16px;
  line-height: 1.6;
  font-family: 'Open Sans', sans-serif;
  padding: 25px 0;
}

a {
  text-decoration: none;
  color: #111111;
  transition: all 200ms ease-in-out;
}

a:hover {
  color: #FF851B;
}

header {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.5);
  margin-bottom: 50px;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-space--between {
  justify-content: space-between;
}

.flex-space--around {
  justify-content: space-around;
}

.flex-align--center {
  align-items: center;
}

.flex-align--end {
  align-items: flex-end;
}

.flex-direction--column {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.fr-header {
  height: 70px;
  padding: 0 25px;
}

.fr-header--logo {
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 21px;
}

.fr-header--nav__list li {
  margin-left: 25px;
  font-size: 14px;
  font-weight: 600;
}

.fr-header--nav__list li a:hover {
  color: #FF851B;
}

.rf-header {
  padding: 25px;
}

.rf-header--logo {
  text-align: center;
  display: block;
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 45px;
  line-height: 1;
  margin-bottom: 15px;
}

.rf-header--nav__list li {
  margin: 0 15px;
}

.rf-header--nav__list li a {
  font-size: 14px;
  font-weight: 600;
}

.rh-header {
  width: 250px;
  height: 600px;
  align-items: center;
  padding: 25px;
}

.rh-header--logo {
  font-family: 'Poppins', 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 21px;
}

.rh-header--nav__list li {
  text-align: center;
  margin-bottom: 15px;
}

.rh-header--copy {
  font-size: 12px;
  font-weight: 700;
}
